
import React, { useState } from "react";
import { Bell } from "lucide-react";
import { useNavigate, useLocation } from "react-router-dom";
import styles from "./GlobalHeader.module.css"; 

export default function GlobalHeader() {
  const [open, setOpen] = useState(false);
  const navigate = useNavigate();
  const location = useLocation();

  const handleNavigate = (path) => {
    navigate(path);
    setOpen(false);
  };

  const titleMap = {
    "/curation": "CROSSXNOTE",
    "/balance-game": "BALANCE GAME",
    "/column": "COLUMN",
    "/qna": "QnA",
    "/mypage": "MY PAGE",
  };

  const currentTitle = titleMap[location.pathname] || "CROSSXNOTE";

  return (
    <header className={styles.header}>
      <div className={styles.headerTop}>
        {/*      */}

        <h1 className={styles.headerLogo} onClick={() => setOpen((v) => !v)}>
          {location.pathname === "/"||"/curation" ? (
            <>
              CROSS<span>X</span>NOTE
            </>
          ) : (
            currentTitle
          )}
        </h1>

        <button className={styles.headerIcon} aria-label="">
          <Bell size={20} />
        </button>
      </div>

      {open && (
        <nav className={styles.headerMenu} aria-label=" ">
          <ul>
            <li onClick={() => handleNavigate("/curation")}>CROSSXNOTE</li>
            <li onClick={() => handleNavigate("/balance-game")}>BALANCE GAME</li>
            <li onClick={() => handleNavigate("/column")}>COLUMN</li>
            <li onClick={() => handleNavigate("/qna")}>QnA</li>
            <li onClick={() => handleNavigate("/mypage")}>MY PAGE</li>
          </ul>
        </nav>
      )}
    </header>
  );
}
