import React, { useState, useEffect } from 'react';
import CheckIcon from './component/checkIcon';
import FilterIcon from '../../components/icons/FilterIcon';
import LogoIcon from './component/logoIcon';
import styles from './ReportRadar.module.css';


import {
  Radar,
  RadarChart,
  PolarGrid,
  PolarAngleAxis,
  PolarRadiusAxis,
  ResponsiveContainer,
  Text,
} from 'recharts';

// ===  API   ===
const fetchChartDataFromAPI = () => {
  return new Promise(resolve => {
    setTimeout(() => {
      resolve({
        radarData: [
          { subject: '', value: 35, fullMark: 50, color: '#2986E6' },
          { subject: '', value: 10, fullMark: 50, color: '#C36839' },
          { subject: '', value: 32, fullMark: 50, color: '#73C62F' },
          { subject: '\n', value: 15, fullMark: 50, color: '#FD88D9' },
          { subject: '', value: 20, fullMark: 50, color: '#9582FF' },
          { subject: '', value: 50, fullMark: 50, color: '#FE817B' },
        ],
        analysisResults: [
          '    !',
          '     .',
          '      !'
        ],
        userName: '',
        maxScore: 50,
      });
    }, 1000);
  });
};

//     
const CustomPolarAngleAxisTick = ({ x, y, payload, data }) => {
  const dataItem = data.find(d => d.subject === payload.value);
  const color = dataItem ? dataItem.color : '#000';
  let dxOffset = 0;
  let dyOffset = 0;

  switch (payload.index) {
    case 0: 
      dyOffset = -6; //   
      dxOffset = 2; //   
      break;
    case 1: 
      dxOffset = 25;
      dyOffset = 0; 
      break;
    case 2: 
      dxOffset = 25;
      dyOffset = 0;
      break;
    case 3: 
      dxOffset = 0;
      dyOffset = -6;
      break;
    case 4:
      dxOffset = -25; 
      dyOffset = 0; 
      break; 
    case 5:
      dxOffset = -25;
      dyOffset = 0;
      break;  
  }

  return (
    <Text
      x={x}
      y={y}
      width={100}
      height={30}
      textAnchor="middle"
      verticalAnchor="middle"
      fill={color}
      fontSize={14}      
      fontWeight="bold"

      dx={dxOffset}
      dy={dyOffset}

      className={styles.radarAxisLabel}
      style={{ transform: payload.index === 3 ? 'translateY(15px)' : '' }}
    >
      {payload.value}
    </Text>
  );
};

//  
const ReportRadar = () => {
  const [chartData, setChartData] = useState([]);
  const [analysis, setAnalysis] = useState([]);
  const [userName, setUserName] = useState('');
  const [maxScore, setMaxScore] = useState(50);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    //   
    const loadData = async () => {
      try {
        const result = await fetchChartDataFromAPI();
        setChartData(result.radarData);
        setAnalysis(result.analysisResults);
        setUserName(result.userName);
        setMaxScore(result.maxScore);
        setIsLoading(false);
      } catch (error) {
        console.error("   :", error);
        setIsLoading(false);
      }
    };
    loadData();
  }, []);

  if (isLoading) {
    return <div className={styles.radarLoading}>  ...</div>;
  }
  
  if (!chartData || chartData.length === 0) {
      return <div className={styles.radarNoData}>  .</div>;
  }

  // 10, 20, 30, 40, 50  ticks  
  const customTicks = [0, 10, 20, 30, 40, 50];

  const headerText = (
      <>
          <span className={styles.radarHeaderLogoWrapper}>
              <LogoIcon className={styles.radarHeaderLogo} /> 
              &nbsp;&nbsp;
          </span>
          {userName}    
      </>
  );

  return (
    <div>
    <div className={styles.radarHeader}>
        <h2 className={styles.radarTitle}>{userName}   </h2>
        <p className={styles.radarSubtitle}>
          {headerText}
        </p>
    </div>

    <div className={styles.radarChartContainer}>
      
      {/* Recharts  */}
      <ResponsiveContainer width="100%" height="80%">
        <RadarChart 
          cx="50%" 
          cy="50%" 
          outerRadius="80%" 
          data={chartData}
          margin={{ top: 10, right: 30, bottom: 20, left: 30 }}
        >
          <PolarGrid stroke="#E6E6E6" stroke-width="1px" radialLines={false} />
          
          <PolarAngleAxis 
            dataKey="subject" 
            tick={<CustomPolarAngleAxisTick data={chartData} />}
          />

          <PolarRadiusAxis 
            angle={90} 
            domain={[0, maxScore]}
            ticks={customTicks}
            className={styles.radarRadiusAxisTick}
            axisLine={false}
            tickFormatter={(value) => (value === 0 ? '' : value)}

            tick={(props) => {
            const { payload, x, y } = props;
            if (payload.value === 0) return null; // 0   
            
            return (
                <Text
                    x={x}
                    y={y}
                    dx={-4} 
                    dy={10}  
                    fontSize={7}
                    fontWeight="bold"
                    fill='#E6E6E6'
                    fillOpacity={0.8}
                >
                    {payload.value}
                </Text>
            );
        }}
          />
          
          <Radar
            name={`${userName}  `}
            dataKey="value"
            stroke="#209b86"
            strokeWidth={2}
            fill="#209b86"
            fillOpacity={0.6}
            dot={false}
            activeDot={false}
          />
        </RadarChart>
      </ResponsiveContainer>

      {/*    */}
      <div className={styles.radarAnalysisSection}>
        {analysis.map((line, index) => {
          // 0, 1  CheckIcon,  FilterIcon ( 2  )
          const isPrimaryLine = index < 2; 
          const IconComponent = isPrimaryLine ? CheckIcon : FilterIcon;
          const iconClassName = isPrimaryLine ? styles.checkIcon : styles.filterIcon;

          return (
            <div 
              key={index} 
              className={`${styles.radarAnalysisLine} ${isPrimaryLine ? styles.primary : styles.tip}`}
            >
              <span className={styles.icon}>
                <IconComponent className={iconClassName} />
              </span> 
              {line}
            </div>
          );
        })}
      </div>
    </div>
    </div>
  );
};

export default ReportRadar;