import React, { useState } from 'react';
import MenuItem from './MenuItem';
import { useNavigate } from 'react-router-dom';
import {useModal} from '../../components/Modal/ModalProvider';
import {presets} from '../../components/Modal/presets';
import styles from './MenuReport.module.css';

const MenuReport = () => {
    //  Hooks 
    const { open } = useModal();
    const nav = useNavigate(); 
    
    //     <<   !!!!! >> 
    const navigateTo = (path) => {
        nav(path);
        console.log(` : ${path}`);
    };
    
    //     
    const handleLogout = async () => {
        const res = await open(presets.logout()); 

        if (res === 'logout') {
            console.log(" API     ...");
            
            // API     
            nav('/login'); //     <<   !!!!! >> 
        }
    };

    //    
    const menuItems = [
        { label: ' ', path: '/edit/info', action: () => navigateTo('/edit/info') },
        { label: '  ', path: '/my/scrap', action: () => navigateTo('/my/scrap') },
        { label: '  ', path: '/my/column', action: () => navigateTo('/my/column') },
        { label: '  QnA', path: '/my/qna', action: () => navigateTo('/my/qna') },
    ];

    return (
        <div className={styles.reportMenuContainer}>
            {menuItems.map((item) => (
                <MenuItem
                    key={item.label}
                    label={item.label}
                    onClick={item.action}
                    styles={styles}
                />
            ))}
            
            <MenuItem
                label=""
                isDestructive={true}
                onClick={handleLogout}
                styles={styles}
            />
        </div>
    );
}

export default MenuReport;