// src/pages/balancegame/CategoryBalanceCard.jsx
import { useState } from "react";
import Button from "../../components/atoms/btn_I/btn_I";
import styles from "./balancegame.module.css";

export default function CategoryBalanceCard({
  title,
  description,
  options,
  onSelectOption,
}) {
  const [selectedCategory, setSelectedCategory] = useState("");
  const [isOpen, setIsOpen] = useState(false);

  const categoryList = [
    "",
    "",
    "/",
    "/",
  ];

  const handleToggle = () => {
    setIsOpen((prev) => !prev);
  };

  const handleCategorySelect = (category) => {
    setSelectedCategory(category);
    setIsOpen(false);
  };

  return (
    <div className={styles.quizStyle2}>
      <div className={styles.titleContainer}>
        <div className={styles.title}>{title}</div>
        <div className={styles.subTitle}>{description}</div>
      </div>

      <div className={styles.card}>
        {/*   */}
        <div className={styles.dropdown}>
          <span>{selectedCategory}</span>

          <button
            type="button"
            className={styles.dropdownArrow}
            onClick={handleToggle}
          >
            {isOpen ? "" : ""}
          </button>
        </div>

        {/*   */}
        {isOpen && (
          <div className={styles.dropdownList}>
            {categoryList.map((category) => (
              <div
                key={category}
                className={styles.dropdownItem}
                onClick={() => handleCategorySelect(category)}
              >
                {category}
              </div>
            ))}
          </div>
        )}

        <p className={styles.categoryDescription}>
                
        </p>

        {/*   */}
        <div className={styles.select}>
          {options.map((opt) => (
            <Button
              key={opt.id}
              appearance="default"
              onClick={() => onSelectOption?.(opt.id)}
            >
              {opt.label}
            </Button>
          ))}
        </div>
      </div>
    </div>
  );
}
