import { createContext, useCallback, useContext, useMemo, useState, useEffect } from 'react';
import Modal from './Modal';

//        Context  (: null)
const ModalContext = createContext(null);

//    Provider 
export function ModalProvider({ children }) {
  //     state
  // - isOpen:  
  // - title/message/variant/buttons:  UI 
  // - _resolve: open()   Promise resolve  
  const [state, setState] = useState({ isOpen: false });

  // /  wrapper   
 useEffect(() => {
   const wrap = document.querySelector('.app-wrapper');
   if (!wrap) return;
   if (state.isOpen) wrap.classList.add('modal-open');
   else wrap.classList.remove('modal-open');
 }, [state.isOpen]);

  //  : (config)  Promise 
  // -  : const result = await open(config)
  const open = useCallback((config) => {
    return new Promise((resolve) => {
      //   + config  , resolve _resolve 
      setState({ isOpen: true, ...config, _resolve: resolve });
    });
  }, []);

  //  : (reason)    Promise 
  const close = useCallback((reason) => {
    setState((s) => {
      // _resolve  (: 'overlay', 'escape', 'close' )
      s._resolve?.(reason ?? 'close');
      //   ()
      return { isOpen: false };
    });
  }, []);

  //    :   value  resolve
  const onAction = useCallback((value) => {
    setState((s) => {
      s._resolve?.(value);      //  value  
      return { isOpen: false }; // 
    });
  }, []);

  //    :     
  const ctx = useMemo(() => ({ open, close }), [open, close]);

  return (
    //   open/close  
    <ModalContext.Provider value={ctx}>
      {children}
      {/*   :  Modal UI  */}
      <Modal
        isOpen={state.isOpen}
        title={state.title}
        message={state.message}
        variant={state.variant}
        buttons={state.buttons}
        onClose={close}      // /ESC    close 
        onAction={onAction}  //    onAction 
      />
    </ModalContext.Provider>
  );
}

//    /   
export function useModal() {
  const ctx = useContext(ModalContext);
  // Provider     (  )
  if (!ctx) throw new Error('useModal  ModalProvider  .');
  return ctx; // { open, close }
}
