//    . 

import React, { useState } from 'react';
import FilterIcon from '../icons/FilterIcon';
import SortLatest from './SortLatest';
import SortPopular from './SortPopular';
import SortComments from './SortComments';
import SearchIcon from './SearchIcon';
import styles from './SearchBar.module.css';

//     (   ,  347px )
function SearchBar1({ maxWidth = '347px', onFilterClick, onSearchChange, onSortChange }) {
  const [searchTerm, setSearchTerm] = useState(''); //      
  const [activeSort, setActiveSort] = useState('latest'); //    
  const [isFocused, setIsFocused] = useState(false); //    

  //    activeSort   
  const handleSortClick = (key) => {
    setActiveSort(key);
    console.log(`  : ${key}`);
    if (onSortChange) onSortChange(key);

  };

  //     
  const handleFilterClick = () => {
    console.log('  ');
    if (onFilterClick) onFilterClick();
  };

  //     (UI )
  const handleFocus = () => {
    setIsFocused(true);
  };

  //      (UI )
  const handleBlur = () => {
    setIsFocused(false);
  };

  const handleSearchInputChange = (e) => {
    const value = e.target.value;
    setSearchTerm(value);
    if (onSearchChange) onSearchChange(value);
  };


  return (
      <div className={styles.searchFilterContainer}
        style={{ maxWidth: maxWidth }} // maxWidth Props  
      > 
        {/* 1.    */}
        <div className={`${styles.searchInputWrapper} ${isFocused ? styles.focused : ''}`}>
          {/* SearchIcon props color ,  SearchBar.module.css  */}
          <SearchIcon color={isFocused ? '#39A2A5' : '#ACADAC'} />
          <input
            type="text"
            placeholder=" ,    "
            value={searchTerm}
            className={styles.searchInput} 
            onChange={handleSearchInputChange}

            onFocus={handleFocus}
            onBlur={handleBlur}
          />
        </div>

        {/* 2.   */}
        <div className={styles.sortFilterArea}>
          
          <div className={styles.sortOptions}>
            {/* SortLatest   styles.sortOptionItem styles.active   props  */}
            <SortLatest 
              isActive={activeSort === 'latest'}
              onClick={() => handleSortClick('latest')}
              styles={styles} //    (SortLatest  )
            />

            <SortPopular 
              isActive={activeSort === 'popular'}
              onClick={() => handleSortClick('popular')}
              styles={styles} //   
            />

            <SortComments 
              isActive={activeSort === 'comments'}
              onClick={() => handleSortClick('comments')}
              styles={styles} //   
            />
          </div>

          {/* 3.   */}
          <button
            onClick={handleFilterClick}
            className={styles.filterButton}
          >
          <FilterIcon /> 
          </button>

        </div>
      </div>
  );
}

export default SearchBar1;