// src/contexts/AuthContext.jsx
import React, { createContext, useContext, useEffect, useState } from "react";
import axios from "axios";

const AuthContext = createContext(null);

export function AuthProvider({ children }) {
  const [accessToken, setAccessToken] = useState(null);
  const [refreshToken, setRefreshToken] = useState(null);
  const [user, setUser] = useState(null);
  const [authLoading, setAuthLoading] = useState(true); 

  //   ( const API_BASE = process.env.REACT_APP_API_URL || "";  )
  const API_BASE = process.env.REACT_APP_API_BASE_URL || "";

  //   
  const fetchMe = async (token = accessToken) => {
    if (!token) return;
    try {
      const res = await axios.get(`${API_BASE}/auth/me`, {
        headers: { Authorization: `Bearer ${token}` },
      });
      // ,    
      setUser(res.data);
    } catch (err) {
      console.error("GET /auth/me ", err);
    } finally {
      setAuthLoading(false);
    }
  };

  //        + /auth/me 
  useEffect(() => {
    const storedAccess = localStorage.getItem("accessToken");
    const storedRefresh = localStorage.getItem("refreshToken");

    if (storedAccess && storedRefresh) {
      setAccessToken(storedAccess);
      setRefreshToken(storedRefresh);
      fetchMe(storedAccess);
    } else {
      setAuthLoading(false);
    }
  }, []);

  //   (/auth/local/login)
  const loginLocal = async (email, password) => {
    const res = await axios.post(`${API_BASE}/auth/local/login`, {
      email,
      password,
    });

    //  
    const { accessToken, refreshToken, tokenType } = res.data;
    localStorage.setItem("tokenType", tokenType);
    await applyTokensAndFetchUser({ accessToken, refreshToken });

    return res.data;
  };

  //  (/auth/logout) ( )
  const logout = async () => {
    try {
      if (accessToken) {
        await axios.post(
          `${API_BASE}/auth/logout`,
          {},
          {
            headers: { Authorization: `Bearer ${accessToken}` },
          }
        );
      }
    } catch (err) {
      //       
      console.error("POST /auth/logout  ( )", err);
    } finally {
      setAccessToken(null);
      setRefreshToken(null);
      setUser(null);

      localStorage.removeItem("accessToken");
      localStorage.removeItem("refreshToken");
      localStorage.removeItem("tokenType");
    }
  };

  //   (/auth/refresh)
  const refreshAccessToken = async () => {
    if (!refreshToken) return null;

    try {
      const res = await axios.post(`${API_BASE}/auth/refresh`, {
        refreshToken,
      });

      const { accessToken: newAccessToken } = res.data;

      setAccessToken(newAccessToken);
      localStorage.setItem("accessToken", newAccessToken);

      return newAccessToken;
    } catch (err) {
      console.error("POST /auth/refresh ", err);
      await logout();
      return null;
    }
  };

  // +      (    )
  const applyTokensAndFetchUser = async ({ accessToken, refreshToken }) => {
  if (!accessToken || !refreshToken) return;

  setAccessToken(accessToken);
  setRefreshToken(refreshToken);

  localStorage.setItem("accessToken", accessToken);
  localStorage.setItem("refreshToken", refreshToken);

  await fetchMe(accessToken);
};
  //   
  const value = {
    user,
    accessToken,
    refreshToken,
    authLoading,
    loginLocal,
    logout,
    refreshAccessToken,
    fetchMe,
    applyTokensAndFetchUser,
    socialLoginUrls: {
      kakao: `${API_BASE}/auth/login/kakao`,
      google: `${API_BASE}/auth/login/google`,
    },
  };

  return (
    <AuthContext.Provider value={value}>{children}</AuthContext.Provider>
  );
}

export function useAuth() {
  return useContext(AuthContext);
}
